import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { Nav, NavItem, NavLink, Fade, Collapse, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, DropdownToggle } from 'reactstrap'
import { getTransactionName, getShareHost, loadAccountInfo, calcAccountId, purchaseResource } from '../js/gegeUtil.js'
import classnames from 'classnames';
import { loadAccountTransactions, loadAutoTransactions } from '../../eshop/eshop.js'
import AccountDialog from '../../conponents/AccountDialog'
const get = require('simple-get')
class AccountList extends Component {

  constructor(props) {
    super(props);
    this.page = 1;
    this.state = { accounts: null, page: 1, transPage: 1, showAccount: false, accountInfo: null, filterValue: '', transactionTab: '0' }
  }

  componentDidMount() {
    this.loadAccountList(1, this.state.filterIdx)
  }

  loadAccountList(page, query) {
    if (!query)
      query = this.state.filterValue;
    let url =  getShareHost() + '/btb?action=listAccount&page=' + page + "&query=" + query;

    get.concat(url, (function (err, res, data) {
      if (err) {
        console.log(err);
        return;
      }
      if (data) {
        try {
          let accounts = JSON.parse(data);
          this.setState({ accounts })
        } catch (e) {

        }

      }
    }).bind(this))
  }

  prePage() {
    if (this.state.page > 1) {
      this.state.page--
      this.loadAccountList(this.state.page)
    }
  }

  nextPage() {
    this.state.page++
    this.loadAccountList(this.state.page)
  }

  renderAccountList() {
    let list = this.state.accounts.accounts;
    if (!list)
      return null;
    let content = list.map((res) => {
      return (<tr>
        <td><AccountDialog rsId={res.rsId} /></td>
        <td>{res.height}</td>
        <td>{res.balance} BTB</td>
        <td>{res.mined} BTB</td>
      </tr>)
    });

    return content;
  }

  renderAutoTransactionItem() {
    let content = this.state.autoTransactions.map((res) => {
      return (<tr>
        <td>{getTransactionName(res.type)}</td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} BTB</td>
        <td>0 BTB</td>
        <td>{res.height}</td>
      </tr>)
    });
    return content;
  }

  renderAccountTransactionItem() {
    let content = this.state.accountTransactions.map((res) => {
      return (<tr>
        <td>{getTransactionName(res.type)}</td>
        <td>{res.from}</td>
        <td>{res.to}</td>
        <td>{res.amount} BTB</td>
        <td>{res.fee} BTB</td>
        <td>{res.height}</td>
      </tr>)
    });

    return content;
  }

  renderAccountTransactions() {

    if (this.state.transactionTab == '0') {
      if (this.state.accountTransactions) {
        return (<Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>Type(交易类型)</th>
              <th>From(发起账户)</th>
              <th>To(接收账户)</th>
              <th>Amount(金额)</th>
              <th>Fee(手续费)</th>
              <th>Height(区块高度)</th>
            </tr>
          </thead>
          <tbody>
            {this.renderAccountTransactionItem()}
          </tbody>
        </Table>)
      } else {
        return null;
      }
    } else {
      //show auto transactions
      if (this.state.accountInfo.autoTransactions) {
        return (<Table hover bordered striped responsive size="sm">
          <thead>
            <tr>
              <th>Type(交易类型)</th>
              <th>From(发起账户)</th>
              <th>To(接收账户)</th>
              <th>Amount(金额)</th>
              <th>Fee(手续费)</th>
              <th>Height(区块高度)</th>
            </tr>
          </thead>
          <tbody>
            {this.renderAutoTransactionItem()}
          </tbody>
        </Table>)
      } else {
        return null;
      }
    }
  }

  onTransactionTabClicked(tab) {
    if (this.state.transactionTab !== tab) {
      this.setState({
        transactionTab: tab,
      });
      //
      if (tab === '0') {
        //load account transactions
        this.loadAccountTransactions(1);
      }
      if (tab === '1') {
        //load auto transaction
        this.loadAutoTransactions(1);
      }
    }
  }

  renderTransactionsTabs() {
    return (<div><Nav tabs>
      <NavItem>
        <NavLink
          className={classnames({ active: this.state.transactionTab === '0' })}
          onClick={() => { this.onTransactionTabClicked('0'); }}
        >
          <i className="icon-calculator"></i><Badge
            color="success">Account Tran.(账户交易) </Badge>
        </NavLink>
      </NavItem>
      <NavItem>
        <NavLink
          className={classnames({ active: this.state.transactionTab === '1' })}
          onClick={() => { this.onTransactionTabClicked('1'); }}
        >
          <i className="icon-basket-loaded"></i><Badge pill color="danger">Auto Tran.(自动交易)</Badge>
        </NavLink>
      </NavItem>
    </Nav>
      {this.renderAccountTransactions()}
      {this.renderTransactionPagnations()}
    </div>
    )
  }

  loadAccountTransactions(page) {
    let rsId = this.state.accountInfo.rsId;
    loadAccountTransactions(rsId, page, 10, ((ok, data) => {
      if (ok && data) {
        let accountTransactions = data.transactions;
        this.setState({ accountTransactions })
      }
    }).bind(this))
  }

  loadAutoTransactions(page) {
    let rsId = this.state.accountInfo.rsId;
    loadAutoTransactions(rsId, page, 10, ((ok, data) => {
      if (ok && data) {
        let autoTransactions = data.transactions;
        this.setState({ autoTransactions })
      }
    }).bind(this))
  }

  preTransPage() {
    if (this.state.transPage > 1) {
      this.state.transPage--
    }
    if (this.state.transactionTab === '0') {
      this.loadAccountTransactions(this.state.transPage)
    } else {
      this.loadAutoTransactions(this.state.transPage);
    }
  }

  nextTransPage() {
    this.state.transPage++
    if (this.state.transactionTab === '0') {
      this.loadAccountTransactions(this.state.transPage)
    } else {
      this.loadAutoTransactions(this.state.transPage);
    }
  }

  renderTransactionPagnations() {
    return (<Pagination>
      <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.preTransPage() }}>-</PaginationLink></PaginationItem>
      <PaginationItem active>
        <PaginationLink tag="button">{this.state.transPage}</PaginationLink>
      </PaginationItem>
      <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextTransPage() }}>+</PaginationLink></PaginationItem>
    </Pagination>)
  }

  rederAccountInfoDialog() {
    if (!this.state.accountInfo)
      return null;
    return (<Modal isOpen={this.state.showAccount} toggle={this.toggleAccount.bind(this)}
      className={'modal-info ' + this.props.className}>
      <ModalHeader toggle={this.toggleAccount.bind(this)}>Account Info.(账户明细)</ModalHeader>
      <ModalBody>
        <p> Account ID(账户ID): {this.state.accountInfo.rsId}</p>
        <p> Account PK(账户公钥): {this.state.accountInfo.pk}</p>
        <p> Account Balance(账户余额): {this.state.accountInfo.balance}</p>
        <p>Transactions(交易列表):</p>
        {this.renderTransactionsTabs()}
      </ModalBody>
      <ModalFooter>
        <Button color="danger" onClick={this.toggleAccount.bind(this)}>OK</Button>
      </ModalFooter>
    </Modal>)
  }

  toggleAccount() {
    this.setState({
      showAccount: !this.state.showAccount,
    });
  }

  showAccountInfo(rsId) {
    this.toggleAccount();
    let url = getShareHost() + '/btb?action=account&rsIdOrPkHex=' + rsId;
    get.concat(url, (function (err, res, data) {
      if (err) {
        // console.log(err);
        return;
      }
      let accountInfo = JSON.parse(data);
      let accountTransactions = accountInfo.transactions;
      this.setState({ accountInfo, accountTransactions })
    }).bind(this))
  }


  updateQueryValue(e) {
    if (e && e.target) {
      let value = e.target.value;
      this.setState({ filterValue: value })
    }
  }

  renderList() {
    let accounts = this.state.accounts;
    if (!accounts)
      return null;
    return (<Row>
      <Col>
        <Card>
          <CardBody>
            <InputGroup>
              <Input placeholder="search(搜索)" onChange={this.updateQueryValue.bind(this)} />
              <Button color="primary" onClick={(e) => this.loadAccountList(1)}>
                <i className="fa fa-search"></i>&nbsp;
                </Button>
            </InputGroup>
            <Table hover bordered striped responsive size="sm">
              <thead>
                <tr>
                  <th>ID(编号)</th>
                  <th>Height(高度)</th>
                  <th>Balance(余额)</th>
                  <th>Miner(矿工收入)</th>
                </tr>
              </thead>
              <tbody>
                {this.renderAccountList()}
                {this.rederAccountInfoDialog()}
              </tbody>
            </Table>
            <nav>
              <Pagination>
                <PaginationItem><PaginationLink previous tag="button" onClick={(e) => { this.prePage() }}>Prev(上一页)</PaginationLink></PaginationItem>
                <PaginationItem active>
                  <PaginationLink tag="button">{this.state.page} / {this.state.accounts.total}</PaginationLink>
                </PaginationItem>
                <PaginationItem><PaginationLink next tag="button" onClick={(e) => { this.nextPage() }}>Next(下一页)</PaginationLink></PaginationItem>
              </Pagination>
            </nav>
          </CardBody>
        </Card>
      </Col>
    </Row>)
  }

  render() {
    return (
      <div className="animated fadeIn">
        {this.renderList()}
      </div>

    );
  }
}

export default AccountList;



